.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: calc(100vh - 11rem);

	&__title {
		color: var(--text-color);
		font-weight: 600;
		font-size: 1.6rem;
		margin: 3.3rem 0 1.6rem 0;
	}
}

.tiles-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	padding: 0;
	margin: 5rem 0;
	list-style-type: none;
	gap: 0.5rem;

	// @include for-tablet-portrait-up {
	// 	width: 16rem;
	// }

	&__tile {
		border-radius: 0.3rem;
		height: 5.5rem;
		width: 5.5rem;
		box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.2),
			-2px -2px 5px 0 rgba(255, 255, 255, 0.2);

		// @include for-tablet-portrait-up {
		// 	height: 5rem;
		// 	width: 5rem;
		// }

		&:nth-child(1),
		&:nth-child(2) {
			background: linear-gradient(
				to bottom right,
				var(--red-light),
				var(--red-dark)
			);
		}

		&:nth-child(3),
		&:nth-child(4) {
			background: linear-gradient(
				to bottom right,
				var(--blue-light),
				var(--blue-dark)
			);
		}

		&:nth-child(5),
		&:nth-child(6) {
			background: linear-gradient(
				to bottom right,
				var(--green-light),
				var(--green-dark)
			);
		}

		&:nth-child(7),
		&:nth-child(8) {
			background: linear-gradient(
				to bottom right,
				var(--yellow-light),
				var(--yellow-dark)
			);
		}

		&:nth-child(9),
		&:nth-child(10) {
			background: linear-gradient(
				to bottom right,
				var(--pink-light),
				var(--pink-dark)
			);
		}

		&:nth-child(11),
		&:nth-child(12) {
			background: linear-gradient(
				to bottom right,
				var(--brown-light),
				var(--brown-dark)
			);
		}

		&:nth-child(13),
		&:nth-child(14) {
			background: linear-gradient(
				to bottom right,
				var(--black-light),
				var(--black-dark)
			);
		}

		&:nth-child(15),
		&:nth-child(16) {
			background: linear-gradient(
				to bottom right,
				var(--purple-light),
				var(--purple-dark)
			);
		}

		&:nth-child(17),
		&:nth-child(18) {
			background: linear-gradient(
				to bottom right,
				var(--light-blue-light),
				var(--light-blue-dark)
			);
		}

		&:nth-child(19),
		&:nth-child(20) {
			background: linear-gradient(
				to bottom right,
				var(--orange-light),
				var(--orange-dark)
			);
		}
	}
}

.start-button {
	cursor: pointer;
	background-color: var(--background-color);
	box-shadow: var(--light-shadow);
	color: var(--text-color);
	border-radius: 0.3rem;
	padding: 0;
	width: 17.4rem;
	height: 5.2rem;
	border: none;
	font-family: 'Nunito', sans-serif;
	font-size: 3rem;
	font-weight: 600;
	text-transform: uppercase;
	outline: none;

	// @include for-tablet-portrait-up {
	// 	margin-top: 7rem;
	// 	width: 25rem;
	// 	height: 6.5rem;
	// 	font-size: 4rem;
	// 	padding: 0;
	// }

	&:active {
		box-shadow: inset 2px 2px 5px 0 rgba(0, 0, 0, 0.3),
			inset -2px -2px 5px 0 rgba(255, 255, 255, 0.2);
	}

	// &.margin-five-rows {
	// 	margin-top: 4rem;

	// 	@include for-tablet-portrait-up {
	// 		margin-top: 5.5rem;
	// 	}
	// }

	// &.margin-six-rows {
	// 	margin-top: 3rem;

	// 	@include for-tablet-portrait-up {
	// 		margin-top: 4rem;
	// 	}
	// }
}

[data-grid-columns='4'] {
	grid-template-columns: repeat(4, 1fr);
}

[data-margin-tiles='9'] {
	margin: 0 9rem 0 0;

	@include for-tablet-portrait-up {
		margin-right: 11rem;
	}
}
